import { Injectable } from '@angular/core';
import { URL_SERVER } from '../../../environments/environment';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { map, concatAll, retryWhen, delayWhen, take } from 'rxjs/operators';
import { timer, Observable } from 'rxjs';

@Injectable({
    providedIn: 'root'
})

export class BiografiaService {

    constructor(private http: HttpClient) { }

    private hanflerError(error: Observable<any>) {

            let intento = 0;

            return error.pipe(
                delayWhen(() => timer(3000)),
                map((errorHttp: HttpErrorResponse) => {

                    intento++;
                    if (intento === 3) {
                        console.log('Entramos a intentos');
                        if (errorHttp instanceof ErrorEvent) {
                            throw new Error('Error de Red');
                        } else {

                            if ( errorHttp.status === 404) {
                               // throw new Error(' Credenciales incorrectas, por favor comprueba la información ');
                            } else {
                               // throw new Error(' No se pudo realizar la conexión, comprueba tu red ');
                            }
                           
                        }
                    }
                }),
                take(3)
            );
    }

    getBiografia(token: any, id?) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token,
                'x-id': id
            })
        };

        return this.http.get(`${URL_SERVER}/biografia`, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        )
    }



    postBiografia(token: any, data: any) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.post(`${URL_SERVER}/biografia`, data, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

}
